import React from 'react'
import "./TracksTrack.css"
import MusicNoteIcon from '@material-ui/icons/MusicNote';
import ExplicitOutlinedIcon from '@material-ui/icons/ExplicitOutlined';

export default function TracksTrack({ name, alt, author, explicit, cover, file, albumIndex, songIndex, setPlayingState }) {
    const playSong = () => {
        var audio = document.querySelector("audio");

        if(file) 
        {
            audio.src = file;
            audio.play();

            setPlayingState({
                name,
                alt,
                author,
                cover,
                file,
                explicit,
                songIndex,
                albumIndex,
                playing: true
            })
        } 
    }

    return (
        <div className="track" onClick={playSong}>
            <div className="track__cover" style={{ backgroundImage: `url(${cover})` }}></div>
            <div className="track__info">
                <h4 className="centered">
                    {name}
                    <span className="feat">{alt}{explicit && <ExplicitOutlinedIcon />}</span>
                </h4>
                <div className="track__author">{author}</div>
            </div>
            <div className="track__right">
                <MusicNoteIcon />
            </div>
        </div>
    )
}
