function App() {
  const [playingState, setPlayingState] = useState({});

  return (
    <BrowserRouter>
      <div className="app">
        <audio hidden></audio>
        <div className="appContainer">
          <Sidebar albums={albums} />
          <Switch>
            <Route path="/album/:id">
              <Tracks albums={albums} playingState={playingState} setPlayingState={setPlayingState} />
            </Route>
            <Route path="/">
              <Tracks albums={albums} playingState={playingState} setPlayingState={setPlayingState} />
            </Route>
          </Switch>
        </div>
      </div>
    </BrowserRouter>
  );
}

export default App;
