import React from 'react'
import "./Sidebar.css"
import YouTubeIcon from '@material-ui/icons/YouTube'
import LanguageIcon from '@material-ui/icons/Language';
import SidebarAlbum from './SidebarAlbum'
import { IconButton } from '@material-ui/core'

export default function Sidebar({ albums }) {
    const openYoutube = () => {
        window.open("https://www.youtube.com/channel/UCvBUYFoA-FiemYXRoXJ3UKg");
    }

    const openSPINMERIIIIGHTROOOUND = () => {
        window.open("https://hlsr.pro/lancer.html");
    }

    return (
        <div className="sidebar">
            <div className="sidebar__header">
                <h2>FreelancerPlayer</h2>
                <div className="sidebar__headerSocial">
                    <IconButton onClick={openYoutube}>
                        <YouTubeIcon />
                    </IconButton>
                    <IconButton onClick={openSPINMERIIIIGHTROOOUND}>
                        <LanguageIcon />
                    </IconButton>
                </div>
            </div>
            <div className="sidebar__albums">
                {
                    albums.map(album => (
                        <SidebarAlbum name={album.name} explicit={album.explicit} alt={album.alt} id={album.id} key={album.id} />
                    ))
                }
            </div>
        </div>
    )
}
