import React from 'react'
import AlbumIcon from '@material-ui/icons/Album';
import ExplicitIcon from '@material-ui/icons/Explicit';
import "./SidebarAlbum.css";
import { Link } from "react-router-dom";

function SidebarAlbum({ name, alt, explicit, id }) {
    return (
        <Link to={`/album/${id}`}>
            <div className="sidebar__albumsAlbum">
                <div className="sidebar__albumsAlbumContainer">
                    <AlbumIcon />
                    <h3>{name}{alt && (<span className="feat">{alt}</span>)}</h3>
                    {
                        explicit && <div className="sidebar__albumsAlbumContainerRight">
                            <ExplicitIcon />
                        </div>
                    }
                </div>
            </div>
        </Link>
    )
}

export default SidebarAlbum
