import React, { useEffect, useState } from 'react'
import "./Tracks.css"
import { IconButton } from "@material-ui/core";
import LibraryMusicIcon from '@material-ui/icons/LibraryMusic';
import ExplicitOutlinedIcon from '@material-ui/icons/ExplicitOutlined';
import FastForwardIcon from '@material-ui/icons/FastForward';
import FastRewindIcon from '@material-ui/icons/FastRewind';
import PauseIcon from '@material-ui/icons/Pause';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import TracksTrack from "./TracksTrack";
import { useParams } from "react-router-dom";

function Tracks({ albums, playingState, setPlayingState }) {
    const [albumID, setAlbumID] = useState(2);
    const [visible, setVisible] = useState(false);
    const [audioProgress, setAudioProgress] = useState({});

    const { id } = useParams();

    const skipForward = () => {
        let currIndex = playingState.songIndex;
        let album = albums[albumID];
        let songs = album.songs;

        if (songs.length > currIndex + 1) {
            playSong(songs[currIndex + 1], album.cover, albumID, currIndex + 1);
        }
    }

    const skipBackward = () => {
        let currIndex = playingState.songIndex;
        let album = albums[albumID];
        let songs = album.songs;

        if (currIndex - 1 >= 0) {
            playSong(songs[currIndex - 1], album.cover, albumID, currIndex - 1);
        }
    }

    const togglePlay = () => {
        var audio = document.querySelector("audio");

        if (playingState.playing) {
            audio.pause();
        } else {
            audio.play();
        }

        setPlayingState({
            ...playingState,
            playing: !audio.paused
        });
    }

    const playSong = ({ name, alt, author, explicit, file }, cover, albumIndex, songIndex) => {
        var audio = document.querySelector("audio");

        if (file) {
            audio.src = file;
            audio.play();

            setPlayingState({
                name,
                alt,
                author,
                cover,
                file,
                explicit,
                songIndex,
                albumIndex,
                playing: true
            })
        }
    }

    useEffect(() => {
        var audio = document.querySelector("audio");

        audio.addEventListener("timeupdate", (e) => {
            setAudioProgress({ currentTime: e.target.currentTime, duration: e.target.duration });
        });

        setVisible(true);
    }, []);

    useEffect(() => {
        if (albumID !== id && albums[id]) setAlbumID(id);
    }, [id, albums, albumID]);

    useEffect(() => {
        console.log(playingState);
    }, [playingState]);

    return visible && (
        <div className="tracks">
            <div className="tracks__header">
                <LibraryMusicIcon />
                <h2>{albums[albumID].name}</h2>
            </div>
            <div className="tracks__body">
                {
                    albums[albumID].songs.map((song, k) => (
                        <TracksTrack name={song.name} alt={song.alt} cover={albums[albumID].cover} author={song.author} explicit={song.explicit} file={song.file} albumIndex={albumID} songIndex={k} setPlayingState={setPlayingState} key={song.id} />
                    ))
                }
            </div>
            {
                playingState.name &&
                (<div className="tracks__footer">
                    <div className="tracks__footerInfo">
                        <div className="tracks__footerControl">
                            <IconButton onClick={skipBackward}>
                                <FastRewindIcon />
                            </IconButton>
                            <IconButton onClick={togglePlay}>
                                {
                                    !playingState.playing ? <PlayArrowIcon /> : <PauseIcon />
                                }
                            </IconButton>
                            <IconButton onClick={skipForward}>
                                <FastForwardIcon />
                            </IconButton>
                        </div>
                        <div className="tracks__footerInfoCover" style={{ backgroundImage: `url(${playingState.cover})` }}></div>
                        <div className="tracks__footerInfoName">
                            <h3>{playingState.name} {playingState.explicit && <ExplicitOutlinedIcon />}</h3>
                            <div className="tracks__footerInfoAuthor">{playingState.author}</div>
                        </div>
                    </div>
                    <div className="tracks__footerBar">
                        <div className="tracks__footerBarProgress" style={{ width: `${audioProgress.currentTime * 100 / audioProgress.duration}%` }}></div>
                    </div>
                </div>)
            }
        </div>
    )
}

export default Tracks
